import React, { Component, useState } from "react";
import Typography from "@material-ui/core/Typography";
import { withContext } from "../Context";
import { withStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import styles from "../Assets/Quick_start_style.js";
import Loader from "react-loader-spinner";

const translations = window.props && window.props.translations;

const cleanValues = (value) => {
  if (Array.isArray(value)) {
    return value.join("-separator-").toLowerCase().split("-separator-");
  } else {
    if (value != null) {
      return value.toLowerCase();
    }
  }
  return value;
};

const TabContainer = withStyles(styles)(
  ({ title, resultList, classes, page }) => {
    return (
      <div className={classes.searchWrapper}>
        <Typography component="div" className={classes.searchTitle}>
          {title}
        </Typography>
        {resultList !== undefined &&
          resultList.map((course, index) => (
            <ListElement
              key={index}
              course={course}
              buttons={
                (course.type === "vodeclic" && vodeclicButtons) ||
                (course.type === "phileas" && phileasButtons) ||
                (!course.type && programsButtons)
              }
            />
          ))}
        {page !== "base" &&
          resultList !== undefined &&
          resultList.length === 0 && <p>No results</p>}
      </div>
    );
  }
);

const getButtonsClasses = (index, length, classes) => {
  let buttonClasses = [classes.actionWrapper, classes.actionBase];
  index === 0 && buttonClasses.push(classes.actionFirst);
  index + 1 === length && buttonClasses.push(classes.actionLast);
  index % 2 === 0
    ? buttonClasses.push(classes.actionLight)
    : buttonClasses.push(classes.actionDark);
  return buttonClasses;
};

const phileasButtons = [
  "email",
  "statistics",
  "rerun",
  "configure",
  "contribute",
  "preview",
];
const vodeclicButtons = [...phileasButtons].filter((el) => el !== "rerun");
const programsButtons = ["email", "statistics", ""];
const openNewTab = ["preview"];

const ListElement = withStyles(styles)(({ course, classes, buttons }) => {
  // course.type means its a course and not a program

  const [loader, setLoader] = useState(false);

  const getFetchOptions = () => {
    // Options for API calls
    return {
      credentials: "same-origin",
      method: "POST",
      headers: {
        "X-CSRFToken": window.props.csrfToken,
      },
    };
  };

  const generateGradeReport = (program) => {
    // Launch report task
    setLoader(true);
    let courseId = JSON.parse(program.program_courses)[0].course_id
    let programName = program.program_name
    fetch(
      window.props.lmsBase + "/courses/" + courseId + "/instructor/api/problem_grade_report",
      getFetchOptions()
    )
      .then((response) => response.json())
      .then(() => checkReportList(courseId, programName));
  };

  const checkReportList = (courseId, programName) => {
    const options = getFetchOptions();
    // Check when report list has new report - means that task is done
    let intervalID = setInterval(function () {
      fetch(
        window.props.lmsBase + "/courses/" + courseId + "/instructor/api/list_report_downloads",
        options
      )
        .then((response) => response.json())
        .then((json) => {
          if (json.downloads.length) {
            console.log(json.downloads[0]);
            downloadURI(window.props.lmsBase + json.downloads[0], programName + "report")
            clearInterval(intervalID);
          } else {
            checkReportList(courseId, programName);
          }
        });
    }, 2000);
  };

  const downloadURI = (uri, name) => {
    var link = document.createElement("a");
    link.download = name;
    link.href = uri;
    link.click();
    setLoader(false);
}


  return (
    <div className={classes.resultWrapper}>
      <Paper className={classes.resultCard}>
        <Grid container>
          <Grid item xs={9} className={classes.resultInfos}>
            <h2 className={classes.resultTitle}>
              {course.type ? course.course_name : course.program_name}
            </h2>
            <div style={{ textTransform: "capitalize" }}>
              {course.type && course.org}
            </div>
            {!course.type && (
              <div className={classes.programCourseList}>
                {JSON.parse(course.program_courses).map((course) => {
                  return (
                    <div
                      key={course.course_id}
                      className={classes.resultCoursesBold}>
                      {course.course_name}:{" "}
                      <span className={classes.resultCoursesLight}>
                        {course.course_id}
                      </span>
                    </div>
                  );
                })}
              </div>
            )}
          </Grid>
          <Grid item xs={3}>
            {buttons.map((buttonName, index) => (
              <div
                key={index}
                style={{
                  height: `${100 / buttons.length}%`,
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "center",
                }}
                className={getButtonsClasses(
                  index,
                  buttons.length,
                  classes
                ).join(" ")}>
                {course.type || (!course.type && index === 0) ? (
                  <a
                    onClick={() => console.log(course)}
                    style={{ color: "white" }}
                    href={course[buttonName + "_url"]}
                    target={openNewTab.includes(buttonName) ? "_blank" : ""}>
                    <span style={{ textTransform: "capitalize" }}>
                      {translations[buttonName] || buttonName}
                    </span>
                  </a>
                ) : !course.type && index === 1 ? (
                  !loader ? (
                    <div
                      onClick={() =>
                        generateGradeReport(course)
                      }>
                      <span style={{ textTransform: "capitalize" }}>
                        {translations[buttonName] || buttonName}
                      </span>
                    </div>
                  ) : (
                    <Loader type="Puff" color="white" height={25} width={25} />
                  )
                ) : (
                  <div></div>
                )}
              </div>
            ))}
          </Grid>
        </Grid>
      </Paper>
    </div>
  );
});

class SearchContent extends Component {
  respectFilters = (course, selectedFilters) => {
    let results = [];
    Object.entries(selectedFilters).forEach((filter) => {
      let is_valid = false;
      const filterName = filter[0];
      const filterValue = cleanValues(filter[1]);
      const courseValues = cleanValues(course[filterName]);

      if (filterValue.includes("all")) {
        is_valid = true;
      } else if (Array.isArray(courseValues)) {
        if (
          filterValue.every((elem) => {
            return courseValues.includes(elem);
          })
        ) {
          is_valid = true;
        }
      } else {
        if (filterValue.includes(courseValues)) {
          is_valid = true;
        }
      }
      if (!window.props.orgs.includes(course.org)) {
        is_valid = false;
      }
      results.push(is_valid);
    });
    return !results.includes(false);
  };

  filterResults = (courses, page, selectedFilters, programs) => {
    if (page === 0) {
      return courses.filter(
        (course) =>
          course.type === "phileas" &&
          this.respectFilters(course, selectedFilters)
      );
    } else if (page === 1) {
      return courses.filter(
        (course) =>
          course.type === "vodeclic" &&
          this.respectFilters(course, selectedFilters)
      );
    } else if (page === 2) {
      return programs;
    } else {
      return [];
    }
  };
  render() {
    const { page, selectedFilters } = this.props;
    const results = this.filterResults(
      window.props.courses,
      page,
      selectedFilters,
      window.props.programs
    );
    return (
      <div>
        {Object.entries(selectedFilters).length === 0 && (
          <TabContainer
            page={"base"}
            title={
              "Choose filters and start research to find all the courses"
            }></TabContainer>
        )}
        {Object.entries(selectedFilters).length > 0 && page === 0 && (
          <TabContainer
            title={"Courses results"}
            resultList={results}
            page={0}></TabContainer>
        )}
        {Object.entries(selectedFilters).length > 0 && page === 1 && (
          <TabContainer
            title={"Vodeclic results"}
            resultList={results}
            page={1}></TabContainer>
        )}
        {Object.entries(selectedFilters).length > 0 && page === 2 && (
          <TabContainer
            title={"Programs results"}
            resultList={results}
            page={2}></TabContainer>
        )}
      </div>
    );
  }
}

export default withContext(SearchContent);
