import React from "react";
import Loader from "react-loader-spinner";
import Button from "@material-ui/core/Button";
import Grid from "@material-ui/core/Grid";
import { Context } from "../../Context";
import CheckCircleOutline from "@material-ui/icons/CheckCircleOutline";
import HighlightOff from "@material-ui/icons/HighlightOff";
import { Link } from "react-router-dom";
import styles from "../../Assets/Quick_start_style.js";
import { withStyles } from "@material-ui/styles";

const translations = window.props && window.props.translations;

function ProgramLoadingPage({ classes }) {
  return (
    <Context.Consumer>
      {({ newProgramResponse, eraseNewProgram }) => (
        <div className={classes.loadingPage}>
          {newProgramResponse ? (
            <CourseCreationResult />
          ) : (
              <>
                <h3>
                  {translations.waitProgramCreation ||
                    "Your course is beeing created please wait."}
                </h3>
                <h4>
                  {translations.mayTakeSomeTimeCreateProgram ||
                    "Creating your program may take some time..."}
                </h4>
                <div
                  onClick={() => eraseNewProgram()}
                  style={{ cursor: "pointer" }}
                >
                  <h4>
                    {translations.mayTakeSomeTimeLinkCreate ||
                      "Click here to reload the page, your program should appear within the next 10 minutes."}
                  </h4>
                </div>
                <Loader
                  type="Puff"
                  color={window.props.colors.mainColor}
                  height="100"
                  width="100"
                />
              </>
            )}
        </div>
      )}
    </Context.Consumer>
  );
}
export default withStyles(styles)(ProgramLoadingPage);

const CourseCreationResult = () => {
  return (
    <Context.Consumer>
      {({ newProgramResponse, newProgram, eraseNewProgram }) => (
        <>
          {newProgramResponse.status !== "error" ? (
            <>
              <div>
                <CheckCircleOutline
                  style={{ fontSize: 90, color: window.props.colors.mainColor }}
                />
              </div>
              <h3>
                {translations.programHasBeenCreated ||
                  "Your program has been created"}
              </h3>
              <h4>
                {translations.stepFields.program_name_title + ": " + newProgram.program_name}
              </h4>
              <Grid
                container
                spacing={6}
                direction="row"
                justify="center"
                alignItems="center"
              >
                <Grid item>
                  <Button
                    href={newProgramResponse.edit_link}
                    onClick={() => eraseNewProgram()}
                  >
                    {translations.seeMyCourse}
                  </Button>
                </Grid>
                <Grid item>
                  <Link to="/">
                    <Button onClick={() => eraseNewProgram()}>
                      {translations.backToHomePage}
                    </Button>
                  </Link>
                </Grid>
              </Grid>
            </>
          ) : (
              <>
                <div>
                  <HighlightOff style={{ fontSize: 90, color: "red" }} />
                </div>
                <h3>{translations.errorWhileCreatingCourse}</h3>
                <h4>{translations.programNotCreated}</h4>
                <Grid
                  container
                  spacing={6}
                  direction="row"
                  justify="center"
                  alignItems="center"
                >
                  <Grid item>
                    <Link to="/">
                      <Button onClick={() => eraseNewProgram()}>
                        {translations.backToHomePage}
                      </Button>
                    </Link>
                  </Grid>
                </Grid>
              </>
            )}
        </>
      )}
    </Context.Consumer>
  );
};
